"use client";

import { AuthContext } from "@/Context/AuthContext";
import React, { use } from "react";

const SellCarFromDesign = () => {
  const { user } = use(AuthContext);
  

  const handleCarSell = (e) => {
    e.preventDefault();

    const target = e.target;
    const name = target.name.value;
    const email = target.email.value;
    const carTypes = target.carTypes.value;
    const condition = target.condition.value;
    const image = target.image.value;
    const registration = target.registration.value;
    const nid = target.nid.value;
    const address = target.address.value;

    console.log({
      name,
      email,
      carTypes,
      condition,
      image,
      registration,
      nid,
      address,
    });
  };

  return (
    <div>
      <div className="header">
        <h2 className="text-2xl font-bold mb-3 text-center">
          Fill the from for selling your car
        </h2>
      </div>

      <form
        className="w-[550px] bg-white px-6 py-4 shadow-lg"
        onSubmit={handleCarSell}
      >
        <div className="card-body">
          {/* 1st column */}
          <fieldset className="fieldset flex justify-center gap-5">
            {/* Name */}
            <div className="flex flex-col w-full">
              <label className="label">Name</label>
              <input
                type="text"
                className="input outline-0"
                defaultValue={user?.displayName}
                name="name"
              />
            </div>
            {/* Email */}
            <div className="flex flex-col w-full">
              <label className="label">Email</label>
              <input
                type="email"
                className="input outline-0"
                defaultValue={user?.email}
                name="email"
              />
            </div>
          </fieldset>

          {/* 2nd column */}
          <fieldset className="fieldset flex justify-center gap-5">
            {/* type of cars */}
            <div className="flex flex-col w-full">
              <label className="label">Types of Car</label>
              <select
                defaultValue="what type car you have?"
                className="select outline-0"
                name="carTypes"
              >
                <option disabled={true}>what type car you have?</option>
                <option>Sedan</option>
                <option>Hatchback</option>
                <option>SUV</option>
                <option>Pickup Truck</option>
                <option>Sports Car</option>
              </select>
            </div>

            {/* Car time */}
            <div className="flex flex-col w-full">
              <label className="label">Car condition</label>
              <select
                defaultValue="car condition"
                className="select outline-0"
                name="condition"
              >
                <option disabled={true}>car condition</option>
                <option>Brand New</option>
                <option>used</option>
              </select>
            </div>
          </fieldset>

          {/* 3rd column */}
          <fieldset className="fieldset flex justify-center gap-5">
            {/* car image */}
            <div className="flex flex-col w-full">
              <label className="label">Car Image</label>
              <input
                type="text"
                className="input outline-0"
                placeholder="give your car image url here"
                name="image"
              />
            </div>

            {/* Car registration */}
            <div className="flex flex-col w-full">
              <label className="label">Car registration Number</label>
              <input
                type="text"
                className="input outline-0"
                placeholder="your car registration number"
                name="registration"
              />
            </div>
          </fieldset>

          {/* 4th column */}
          <fieldset className="fieldset space-y-2">
            <div className="flex flex-col">
              <label className="label">Your NID number</label>
              <input
                type="text"
                className="input outline-0 w-full"
                placeholder="NID Number"
                name="nid"
              />
            </div>

            <div className="flex flex-col">
              <label className="label">Your Address</label>
              <input
                type="text"
                className="input outline-0 w-full"
                placeholder="Your Address"
                name="address"
              />
            </div>
          </fieldset>
          <button className="btn btn-neutral mt-4">Upload</button>
        </div>
      </form>
    </div>
  );
};

export default SellCarFromDesign;
